import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'  //引入根目录下的hello.vue组件
import Hi from '@/components/Hi'

Vue.use(Router)  //全局使用vue

export default new Router({
  routes: [   //路由配置  这里是一个数组
/*    {       //每一个链接都是一个对象
      path: '/',  //链接路径
      name: 'Hello',  //路由的名称
      component: Hello //对应的组件模板
    },*/
    {
      path: '/hi',
      name: 'Hi',
      component: Hi
    }
  ]
})



